*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
}
@media (max-width: 1320px) {
  .container {
    min-width: 768px;
    max-width: 1320px;
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 320px;
    padding: 0 25px;
  }
}

.header {
  min-height: 880px;
  background: url("../../img/background-desktop.jpg") center center no-repeat;
}
@media (min-resolution: 2dppx) and (min-width: 1024px) {
  .header {
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/background-desktop@2x.jpg") center center no-repeat;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), image-set(url("../img/background-desktop@2x.webp") 2x) center center no-repeat;
    background-size: cover;
  }
}
@media (max-width: 1023px) {
  .header {
    min-height: 640px;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/background-tablet.jpg") center center no-repeat;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), image-set(url("../img/background-tablet.webp") 1x) center center no-repeat;
    background-size: cover;
  }
}
@media (min-resolution: 2dppx) and (max-width: 1023px) {
  .header {
    min-height: 640px;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/background-tablet@2x.jpg") center center no-repeat;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), image-set(url("../img/background-tablet@2x.webp") 2x) center center no-repeat;
    background-size: cover;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 560px;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/background-mobile.jpg") center center no-repeat;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), image-set(url("../img/background-mobile.webp") 1x) center center no-repeat;
    background-size: cover;
  }
}
@media (min-resolution: 2dppx) and (max-width: 767px) {
  .header {
    min-height: 560px;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/background-mobile@2x.jpg") center center no-repeat;
    background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), image-set(url("../img/background-mobile@2x.webp") 2x) center center no-repeat;
    background-size: cover;
  }
}

.header__nav {
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header__nav {
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .header__nav {
    min-height: 70px;
  }
}

@media (max-width: 767px) {
  .header img {
    width: 35px;
    height: 35px;
  }
}

.nav__wrapper {
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}
@media (max-width: 1023px) {
  .nav__wrapper {
    padding-top: 20px;
    padding-bottom: 0;
  }
}
@media (max-width: 767px) {
  .nav__wrapper {
    padding-top: 15px;
    padding-bottom: 0;
  }
}

.nav__wrapper ul {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, 225px);
  align-self: center;
  flex-grow: 2;
  gap: 30px;
  justify-content: center;
}
@media (max-width: 1023px) {
  .nav__wrapper ul {
    padding-right: 11px;
    padding-left: 6vw;
    grid-template-columns: repeat(auto-fit, 40px);
    gap: 20px;
    justify-content: right;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .nav__wrapper ul {
    padding-right: 0;
  }
}

.nav__wrapper a {
  flex-shrink: 0;
  color: #f2f5f6;
}
@media (max-width: 767px) {
  .nav__wrapper a {
    align-self: center;
  }
}

.nav__link {
  line-height: 18px;
  padding-left: 35px;
  position: relative;
}
.nav__link--mail::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../../img/svg/mail.svg");
}
.nav__link--phone::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../../img/svg/phone.svg");
}

.nav__button {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  font-family: "Roboto Condensed", "roboto", "arial", sans-serif;
  text-transform: uppercase;
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  align-self: center;
  letter-spacing: 0.05em;
  margin-right: 63px;
}
@media (max-width: 1023px) {
  .nav__button {
    display: none;
  }
}

@media (max-width: 1023px) {
  .nav__link span {
    display: none;
  }
}

.caption {
  color: #f2f5f6;
  margin-top: 185px;
  padding-left: 125px;
}
@media (max-width: 1023px) {
  .caption {
    margin-top: 81px;
  }
}

.caption h1 {
  font-family: "Roboto Condensed", "roboto", "arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  margin: 0;
}
@media (max-width: 1023px) {
  .caption h1 {
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .caption h1 {
    font-size: 36px;
    line-height: 42px;
  }
}

.caption p {
  margin: 0;
  margin-top: 30px;
  margin-bottom: 48px;
  font-size: 22px;
  line-height: 34px;
  padding-right: 400px;
}
@media (max-width: 1023px) {
  .caption p {
    margin: 0;
    padding-right: 0;
    margin-bottom: 56px;
  }
}
@media (max-width: 767px) {
  .caption p {
    font-size: 14px;
    line-height: 22px;
    margin-top: 7px;
    margin-bottom: 45px;
  }
}

.caption a {
  display: flex;
  flex-wrap: wrap;
  color: #f2f5f6;
  padding: 17px 25px;
  background: #00B2FF;
  font-family: "Roboto Condensed", "roboto", "arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 3px;
  justify-content: center;
  width: 410px;
}
@media (max-width: 1023px) {
  .caption a {
    margin-left: 44px;
  }
}
@media (max-width: 767px) {
  .caption a {
    font-size: 14px;
    line-height: 16px;
    padding: 11px 37px;
    width: 270px;
    margin-left: 0;
  }
}

@media (max-width: 767px) {
  .caption__link--desktop {
    display: none;
  }
}
.caption__link--mobile {
  display: none;
}
@media (max-width: 767px) {
  .caption__link--mobile {
    display: block;
  }
}

.advantage {
  margin-top: 120px;
}

.advantage ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}
@media (max-width: 767px) {
  .advantage ul {
    grid-template-columns: 1fr;
  }
}

.advantage li {
  border: 1px solid #EBEBEB;
  border-left: 5px solid #00B2FF;
  padding: 40px;
  min-height: 300px;
}
.advantage li:hover {
  background-color: #00B2FF;
  color: #f2f5f6;
}
.advantage li:hover h3 {
  color: #f2f5f6;
}

.advantage__card {
  padding-top: 55px;
  padding-bottom: 25px;
}

.advantage__card h3 {
  margin: 0;
  font-family: "Roboto Condensed", "roboto", "arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00B2FF;
  margin-bottom: 15px;
}

.advantage__card p {
  margin: 0;
  line-height: 22px;
}